import "./main.scss";
import Title from './templates/title/title';
import Modules from './templates/modules/modules';
import Fonts from './templates/fonts/fonts';
import Grid from './templates/grid/grid';
import Pages from './templates/pages/pages';
import Adaptive from './templates/adaptive/adaptive';

document.addEventListener( 'DOMContentLoaded', event => {

  const title = new Title;
  const titleShowImage = title.showImage();
  const titleInfo = title.titleInfo();

  const modules = new Modules;
  const modulesShowInfo = modules.showInfo();
  const modulesShowItems = modules.showItems();

  const fonts = new Fonts;
  const fontsShowInfo = fonts.showInfo();

  const grid = new Grid;
  const gridShowInfo = grid.showInfo();
  const gridShowDevices = grid.showDevices();

  const pages = new Pages;
  const pagesShowInfo = pages.showInfo();
  const pagesShowItems = pages.showItems();

  const adaptive = new Adaptive;
  const adaptiveShowInfo = adaptive.showInfo();
  const adaptiveShowPhones = adaptive.showPhones();
  const adaptiveShowCards = adaptive.showCards();
  
  window.addEventListener('scroll', event => {
    const scroll = window.pageYOffset;
    
    titleShowImage(scroll);
    titleInfo(scroll);

    modulesShowInfo(scroll);
    modulesShowItems(scroll);

    fontsShowInfo(scroll);

    gridShowInfo(scroll);
    gridShowDevices(scroll);

    pagesShowInfo(scroll);
    pagesShowItems(scroll);

    adaptiveShowInfo(scroll);
    adaptiveShowPhones(scroll);
    adaptiveShowCards(scroll);
  });
});