export default class Title {
  constructor() {
    this.image = document.querySelector('.js-titleImage');
    this.info = document.querySelector('.js-titleInfo');
  }

  showImage() {
    return function(scroll) {
      const screenHeight = window.innerHeight;
      const top = this.image.getBoundingClientRect().top;
      let position = scroll + top - screenHeight * 20;

      if (position < scroll) {
        this.image.classList.add('active');
      } else {
        this.image.classList.remove('active');
      }
    }.bind(this)
  }

  titleInfo() {
    return function(scroll) {
      const screenHeight = window.innerHeight;
      const top = this.info.getBoundingClientRect().top;
      let position = scroll + top - screenHeight;

      if (position < scroll) {
        this.info.classList.add('active');
      } else {
        this.info.classList.remove('active');
      }
    }.bind(this)
  }
}