export default class Pages {
  constructor() {
    this.info = document.querySelector('.js-pagesTitle');
    this.items = [...document.querySelectorAll('.js-pagesItem')];
  }

  showInfo() {
    return function(scroll) {
      const screenHeight = window.innerHeight;
      const top = this.info.getBoundingClientRect().top;
      let position = scroll + top - screenHeight / 1.5;

      if (position < scroll) {
        this.info.classList.add('active');
      } else {
        this.info.classList.remove('active');
      }
    }.bind(this)
  }

  showItems() {
    return function(scroll) {
      this.items.forEach(item => {
        const screenHeight = window.innerHeight;
        const top = item.getBoundingClientRect().top;
        let position = scroll + top - screenHeight;

        if (position < scroll) {
          item.classList.add('active');
        } else {
          item.classList.remove('active');
        }
      })
    }.bind(this)
  }
}