export default class Grid {
  constructor() {
    this.info = document.querySelector('.js-gridInfo');
    this.devices = document.querySelector('.js-gridDevices');
  }

  showInfo() {
    return function(scroll) {
      const screenHeight = window.innerHeight;
      const top = this.info.getBoundingClientRect().top;
      let position = scroll + top - screenHeight / 1.5;

      if (position < scroll) {
        this.info.classList.add('active');
      } else {
        this.info.classList.remove('active');
      }
    }.bind(this)
  }

  showDevices() {
    return function(scroll) {
      const screenHeight = window.innerHeight;
      const top = this.devices.getBoundingClientRect().top;
      let position = scroll + top - screenHeight / 2;

      if (position < scroll) {
        this.devices.classList.add('active');
      } else {
        this.devices.classList.remove('active');
      }
    }.bind(this)
  }
}