export default class Adaptive {
  constructor() {
    this.info = document.querySelector('.js-adaptiveTitle');
    this.phones = [...document.querySelectorAll('.js-adaptivePhone')];
    this.cards = [...document.querySelectorAll('.js-adaptiveItem')];
  }

  showInfo() {
    return function(scroll) {
      const screenHeight = window.innerHeight;
      const top = this.info.getBoundingClientRect().top;
      let position = scroll + top - screenHeight * .6;

      if (position < scroll) {
        this.info.classList.add('active');
      } else {
        this.info.classList.remove('active');
      }
    }.bind(this)
  }

  showPhones() {
    return function(scroll) {
      const parent = document.querySelector('.js-adaptivePhoneParent');
      const screenHeight = window.innerHeight;
      const top = parent.getBoundingClientRect().top;
      let position = scroll + top - screenHeight / 5;

      if (position < scroll) {
        parent.classList.add('active');
      } else {
        parent.classList.remove('active');
      }
    }.bind(this)
  }

  showCards() {
    return function(scroll) {
      this.cards.forEach(item => {
        const screenHeight = window.innerHeight;
        const top = item.getBoundingClientRect().top;
        let position = scroll + top - screenHeight * .5;

        if (position < scroll) {
          item.classList.add('active');
        } else {
          item.classList.remove('active');
        }
      })
    }.bind(this)
  }
}